import React from "react";
import {Button} from "@material-ui/core";

import Share from '@material-ui/icons/ShareRounded';

import { withStyles } from "@material-ui/core";

import {Link} from '../../../../Components';
import Frame from '../../EachCard/Components/Frame';
import ShareButton from '../../EachCard/Components/Share';
import Image from './Image';

let styles = theme=>({
    root: {
        flex: '1',
        display: 'flex',
        position: 'relative',
        padding: '16px'
    },
    //框架(标题)
    frame: {
        flex: '1',
        position: 'relative',
        overflow: 'hidden',
        padding: '0'
    },
    //内容文字
    content: {
        color: 'inherit',
        display: 'box',
        boxOrient: 'vertical',
        lineHeight: '16px',
        overflow: 'hidden',
        wordBreak: 'break-all',
        whiteSpace: 'normal',
        textOverflow: 'ellipsis',
        lineClamp: 2,
    }
});

let info = {
    title: '为什么计算机人才这么少?',
    to: '/home',
    content: `start:必需。规定从何处开始选取。如果是负数，那么它规定从数组尾部开始算起的位置。也就是说，-1 指最后一个元素，-2 指倒数第二个元素，以此类推。end:可选。规定从何处结束选取。该参数是数组片断结束处的数组下标。如果没有指定该参数，那么切分的数组包含从 start 到数组结束的所有元素。`,
    hot: 111,
    imgsrc: ''
}

class Content extends React.Component{ 
    render(){
        let {classes} = this.props;
        return <div className={classes.root}>
            <Frame content={info} root={classes.frame}>
                <Link
                    to={info.to}
                    className={classes.content}
                    target="_blank"
                    underline="none"
                >{info.content}
                </Link>
                <div style={{
                    position: 'absolute',
                    bottom: 0,
                    display: 'flex',
                    alignItems: 'center'
                    //margin: '10px 5px'
                }}>
                    <span style={{
                        marginRight: '20px',
                        display: 'inline-flex',
                        alignItems: 'center'
                    }}>
                        <Share />{`${info.hot} 万热度`}
                    </span>
                    <ShareButton />
                </div>
            </Frame>
            <Image
                src={info.imgsrc}
                to={info.to}
            />
        </div>
    }

    handleClick = {
        hotClick: ()=>{
            console.log("hot");
        },
    }
}

export default withStyles(styles)(Content);