import React from "react";
import {Grid} from "@material-ui/core";

import { withStyles } from "@material-ui/core";

import AvatarWithPop from '../../EachCard/Components/Comment/Avatar';

import Share from '@material-ui/icons/ShareRounded';

let styles = theme=>({
    root: {
        padding: '5px 16px',
        display: 'flex',
        alignItems: 'center',
    },
    avatar: {
        marginLeft: 0,
        marginRight: 10
    }
});

let comment = {
    avatar: '',
    usrname: '匿名用户',
    usrtag: <Share />,
    usrdesc: "XX在读, XX博士导师"
}

const AuthorInfo = (props)=>{
    let {classes, content} = props;
    return <div className={classes.root}>
        <AvatarWithPop comment={comment} root={classes.avatar}/>
        {comment.usrname}
        {comment.usrtag}，
        {comment.usrdesc}
    </div>
}

export default withStyles(styles)(AuthorInfo);